import {
  AVAILABLE_SHIPPING_METHOD_FRAGMENT,
  SELECTED_SHIPPING_METHOD_FRAGMENT,
} from './ShippingMethodFragment.graphql';

export const BILLING_CART_ADDRESS_FRAGMENT = /* GraphQL */ `
  fragment BILLING_CART_ADDRESS_FRAGMENT on BillingCartAddress {
    city
    company
    country {
      code
      label
    }
    custom_attributes {
      ... on AttributeValue {
        code
        value
      }
    }
    fax
    firstname
    id
    lastname
    middlename
    postcode
    prefix
    region {
      region_id
      code
      label
    }
    street
    suffix
    telephone
    uid
    vat_id
  }
`;

export const SHIPPING_CART_ADDRESS_FRAGMENT = /* GraphQL */ `
  fragment SHIPPING_CART_ADDRESS_FRAGMENT on ShippingCartAddress {
    available_shipping_methods {
      ...AVAILABLE_SHIPPING_METHOD_FRAGMENT
    }
    city
    company
    country {
      code
      label
    }
    custom_attributes {
      ... on AttributeValue {
        code
        value
      }
    }
    fax
    firstname
    id
    lastname
    middlename
    postcode
    prefix
    region {
      region_id
      code
      label
    }
    same_as_billing
    selected_shipping_method {
      ...SELECTED_SHIPPING_METHOD_FRAGMENT
    }
    street
    suffix
    telephone
    uid
    vat_id
  }

  ${AVAILABLE_SHIPPING_METHOD_FRAGMENT}
  ${SELECTED_SHIPPING_METHOD_FRAGMENT}
`;
