import {
  NEGOTIABLE_QUOTE_BILLING_ADDRESS_FRAGMENT,
  NEGOTIABLE_QUOTE_SHIPPING_ADDRESS_FRAGMENT,
} from './NegotiableQuoteAddressFragment.graphql';

import {
  AVAILABLE_PAYMENT_METHOD_FRAGMENT,
  SELECTED_PAYMENT_METHOD_FRAGMENT,
} from './PaymentMethodFragment.graphql';

export const NEGOTIABLE_QUOTE_FRAGMENT = /* GraphQL */ `
  fragment NEGOTIABLE_QUOTE_FRAGMENT on NegotiableQuote {
    available_payment_methods {
      ...AVAILABLE_PAYMENT_METHOD_FRAGMENT
    }
    billing_address {
      ...NEGOTIABLE_QUOTE_BILLING_ADDRESS_FRAGMENT
    }
    email
    is_virtual
    name
    selected_payment_method {
      ...SELECTED_PAYMENT_METHOD_FRAGMENT
    }
    shipping_addresses {
      ...NEGOTIABLE_QUOTE_SHIPPING_ADDRESS_FRAGMENT
    }
    status
    total_quantity
    uid
  }

  ${NEGOTIABLE_QUOTE_BILLING_ADDRESS_FRAGMENT}
  ${NEGOTIABLE_QUOTE_SHIPPING_ADDRESS_FRAGMENT}
  ${AVAILABLE_PAYMENT_METHOD_FRAGMENT}
  ${SELECTED_PAYMENT_METHOD_FRAGMENT}
`;
